"use client";
import { useState, useEffect } from 'react';
import { Plus, Trash2 } from 'lucide-react';

export default function NPVCalculator() {
  const [initialInvestment, setInitialInvestment] = useState(100000);
  const [discountRate, setDiscountRate] = useState(10);
  const [cashFlows, setCashFlows] = useState([20000, 30000, 40000, 50000, 60000]);
  const [npv, setNpv] = useState(0);

  useEffect(() => {
    let totalNPV = -initialInvestment;
    const r = discountRate / 100;

    cashFlows.forEach((cf, index) => {
      totalNPV += cf / Math.pow(1 + r, index + 1);
    });

    setNpv(Math.round(totalNPV));
  }, [initialInvestment, discountRate, cashFlows]);

  const addCashFlow = () => setCashFlows([...cashFlows, 0]);
  const updateCashFlow = (index, value) => {
    const newFlows = [...cashFlows];
    newFlows[index] = Number(value);
    setCashFlows(newFlows);
  };
  const removeCashFlow = (index) => {
    setCashFlows(cashFlows.filter((_, i) => i !== index));
  };

  return (
    <div className="max-w-6xl mx-auto bg-white rounded-3xl shadow-xl overflow-hidden border border-gray-100 mt-10">
      <div className="bg-[#0052cc] p-6 text-white text-center">
        <h2 className="text-2xl font-bold tracking-tight">NPV Calculator</h2>
        <p className="text-blue-100 text-sm opacity-90">Net Present Value of your Investments</p>
      </div>

      <div className="p-8 grid grid-cols-1 lg:grid-cols-2 gap-12">
        {/* Left Side: Inputs */}
        <div className="space-y-6">
          <div className="p-4 bg-gray-50 rounded-xl space-y-4">
            <label className="text-sm font-bold text-gray-600 block uppercase">Initial Investment (Year 0)</label>
            <div className="flex items-center bg-white border border-gray-200 rounded-lg p-3 shadow-sm">
              <span className="text-gray-400 font-bold mr-2">‚Çπ</span>
              <input 
                type="number" value={initialInvestment} 
                onChange={(e) => setInitialInvestment(Number(e.target.value))}
                className="w-full font-bold text-blue-700 outline-none"
              />
            </div>
          </div>

          <div className="p-4 bg-gray-50 rounded-xl space-y-4">
            <label className="text-sm font-bold text-gray-600 block uppercase">Discount Rate (%)</label>
            <input 
              type="range" min="1" max="50" step="0.5" 
              value={discountRate} onChange={(e) => setDiscountRate(Number(e.target.value))}
              className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer accent-blue-600"
            />
            <div className="text-right font-bold text-blue-700">{discountRate}%</div>
          </div>

          <div className="space-y-4">
            <div className="flex justify-between items-center px-1">
              <label className="text-sm font-bold text-gray-600 uppercase">Annual Cash Flows</label>
              <button onClick={addCashFlow} className="flex items-center gap-1 text-xs bg-green-500 text-white px-3 py-1.5 rounded-full font-bold hover:bg-green-600 transition-all">
                <Plus size={14} /> Year {cashFlows.length + 1}
              </button>
            </div>
            <div className="max-h-60 overflow-y-auto space-y-3 pr-2 scrollbar-thin scrollbar-thumb-gray-200">
              {cashFlows.map((cf, idx) => (
                <div key={idx} className="flex items-center gap-3">
                  <span className="text-xs font-bold text-gray-400 w-16">Year {idx + 1}</span>
                  <div className="flex-1 flex items-center bg-white border border-gray-200 rounded-lg p-2.5">
                    <span className="text-gray-400 text-sm mr-2">‚Çπ</span>
                    <input 
                      type="number" value={cf} 
                      onChange={(e) => updateCashFlow(idx, e.target.value)}
                      className="w-full font-semibold text-gray-700 outline-none"
                    />
                  </div>
                  <button onClick={() => removeCashFlow(idx)} className="text-red-400 hover:text-red-600">
                    <Trash2 size={18} />
                  </button>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Right Side: Results */}
        <div className="flex flex-col justify-center items-center">
          <div className={`p-10 rounded-full w-72 h-72 border-8 flex flex-col justify-center items-center transition-all ${npv >= 0 ? 'border-green-500 bg-green-50' : 'border-red-500 bg-red-50'}`}>
            <p className="text-gray-500 text-xs font-bold uppercase mb-2">Net Present Value</p>
            <p className={`text-3xl font-black ${npv >= 0 ? 'text-green-700' : 'text-red-700'}`}>
              ‚Çπ{npv.toLocaleString()}
            </p>
            <span className={`mt-2 text-[10px] font-bold px-3 py-1 rounded-full text-white ${npv >= 0 ? 'bg-green-500' : 'bg-red-500'}`}>
              {npv >= 0 ? 'PROFITABLE' : 'UNPROFITABLE'}
            </span>
          </div>

          <div className="mt-12 w-full space-y-4">
            <div className="bg-blue-50 p-6 rounded-2xl border border-blue-100">
              <h4 className="text-[#0052cc] font-bold text-sm mb-2 flex items-center gap-2">
                üí° Investment Insight
              </h4>
              <p className="text-gray-600 text-sm leading-relaxed">
                {npv >= 0 
                  ? "‡§Ø‡§π ‡§™‡•ç‡§∞‡•ã‡§ú‡•á‡§ï‡•ç‡§ü ‡§Ü‡§™‡§ï‡•á ‡§°‡§ø‡§∏‡•ç‡§ï‡§æ‡§â‡§Ç‡§ü ‡§∞‡•á‡§ü ‡§∏‡•á ‡§Ö‡§ß‡§ø‡§ï ‡§∞‡§ø‡§ü‡§∞‡•ç‡§® ‡§¶‡•á ‡§∞‡§π‡§æ ‡§π‡•à‡•§ ‡§á‡§∏‡§Æ‡•á‡§Ç ‡§®‡§ø‡§µ‡•á‡§∂ ‡§ï‡§∞‡§®‡§æ ‡§´‡§æ‡§Ø‡§¶‡•á‡§Æ‡§Ç‡§¶ ‡§π‡•ã ‡§∏‡§ï‡§§‡§æ ‡§π‡•à‡•§" 
                  : "‡§Ø‡§π ‡§™‡•ç‡§∞‡•ã‡§ú‡•á‡§ï‡•ç‡§ü ‡§Ü‡§™‡§ï‡•á ‡§°‡§ø‡§∏‡•ç‡§ï‡§æ‡§â‡§Ç‡§ü ‡§∞‡•á‡§ü ‡§ï‡•á ‡§Æ‡•Å‡§ï‡§æ‡§¨‡§≤‡•á ‡§ò‡§æ‡§ü‡•á ‡§ï‡§æ ‡§∏‡•å‡§¶‡§æ ‡§≤‡§ó ‡§∞‡§π‡§æ ‡§π‡•à‡•§ ‡§ï‡•É‡§™‡§Ø‡§æ ‡§¶‡•ã‡§¨‡§æ‡§∞‡§æ ‡§µ‡§ø‡§ö‡§æ‡§∞ ‡§ï‡§∞‡•á‡§Ç‡•§"}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}